/*~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~*\
    conferencesuite  |  Legacy theme
\*~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~*/
/*================================*\
    legacy.less
\*================================*/
/**
 * ////////////////    CONTENTS    ////////////////
 * SETTINGS & TOOLS--------------------PROJECT SETTINGS & MIXINS
 * __settings.less.....................variables
 * __tool.buttonText.less..............mixin for button text styles (used on many things)
 *
 * BASE--------------------------------BARE UNCLASSED ELEMENTS
 * _dev.css............................fixes for imported 3rd party
 * _base.css...........................unclassed html elements
 *
 * OBJECTS-----------------------------REUSABLE STRUCTURAL PATTERNS
 * Constrain...........................keeps content at max width
 * MainCol.............................main column of a site page
 * SideCol.............................side column of a site page
 * MobileBar...........................top bar on mobile devices
 * Tabs................................tabbed panel interface
 * Drawer..............................pull-out container
 *
 * COMPONENTS--------------------------SPECIFIC STYLE PATTERNS
 * Banner..............................full-bleed container with background color
 * ConferenceTitles....................container for name, tagline, detail of conference
 * SiteLogo............................logo for conference site (usually an Institute shield)
 * TopImg..............................top banner image containing
 * YearBadge...........................badge at top indicating year
 * NavBar..............................any navigation bar
 * Toggle..............................toggle button for Drawer Object
 * AsceLogo............................company logo in copyright line
 * Callout.............................aside section, in sidebar
 * FooterPanel.........................panel in tabbed footer
 * Sitemap.............................footer navigation
 * SocialListFooter....................social media icons in footer
 * Hero................................hero section on homepage
 * KeynoteSpeaker......................keynote speaker promoted on homepage
 * Chair...............................conference chair
 * SupporterBlock......................block of supporters in one level
 * DateBlock...........................container for important dates
 * Button..............................standard link button
 *
 * THEMES & HELPERS--------------------LOCATION-DEPENDENT STYLES
 * _theme.less.........................overrides for any Base, Object, or Component styles
 * Content.............................main page content (CMS-editable)
 * Spaced..............................add horizontal space, preventing full-bleed
 *
 * ATOMS-------------------------------FUNCTIONAL CSS
 */
